// URLå°åé²æ³¨å¥ éå¯¹æ¥è¯¢åæ°
validator.pushRule('sqlXss', function (val = '', rval = true) {
  if (rval) {
    const re = /select|update|delete|truncate|join|union|exec|insert|drop|count|script|<|>|'|"|=|;/gi;
    const status = re.test(val)
    if (status) {
      return false
    }
  }
  return true
})
